import router from '@/router';
import Vue from 'vue/dist/vue.min.js';
import toastr from 'toastr';
export default {
  props: ['index', 'detail'],
  components: {},
  data: function data() {
    return {
      loading: false,
      loadingAction: false,
      dossierDetail: {},
      dossierDetailMotcua: '',
      listHistoryProcessing: [],
      dossierActions: [],
      tailieuNop: [],
      tailieuKeyQua: [],
      votingItems: [],
      loadingVoting: false,
      headers: [{
        text: '#',
        align: 'center',
        sortable: false
      }, {
        text: 'Vai trò',
        align: 'center',
        sortable: false,
        class: 'vaitro_column'
      }, {
        text: 'Công việc',
        align: 'center',
        sortable: false,
        class: 'congviec_column'
      }, {
        text: 'Thời hạn quy định',
        align: 'center',
        sortable: false,
        class: 'thoihanquydinh_column'
      }, {
        text: 'Ngày bắt đầu',
        align: 'center',
        sortable: false,
        class: 'ngaybatdau_column'
      }, {
        text: 'Người thực hiện',
        align: 'center',
        sortable: false,
        class: 'nguoithuchien_column'
      }, {
        text: 'Kết quả',
        align: 'center',
        sortable: false,
        class: 'ketqua_column'
      }],
      headersMobile: [{
        text: 'TT',
        align: 'center',
        sortable: false
      }, {
        text: 'Công việc',
        align: 'center',
        sortable: false,
        class: 'congviec_column'
      }, {
        text: 'Bộ phận xử lý',
        align: 'center',
        sortable: false,
        class: 'vaitro_column'
      }, {
        text: 'Ngày bắt đầu',
        align: 'center',
        sortable: false,
        class: 'ngaybatdau_column'
      }, {
        text: 'Kết quả',
        align: 'center',
        sortable: false,
        class: 'ketqua_column'
      }],
      isMobile: false,
      two_system: true,
      paymentInfo: false
    };
  },
  computed: {
    secretCode: function secretCode() {
      return this.$store.getters.getScretCode;
    }
  },
  beforeDestroy: function beforeDestroy() {
    if (typeof window !== 'undefined') {
      window.removeEventListener('resize', this.onResize, {
        passive: true
      });
    }
  },
  mounted: function mounted() {
    var vm = this;
    vm.onResize();
    window.addEventListener('resize', vm.onResize, {
      passive: true
    });

    if (vm.isMobile) {
      $('section#content').css('padding-left', '0px');
    }
  },
  created: function created() {
    var vm = this;
    vm.$nextTick(function () {
      vm.onResize();
      window.addEventListener('resize', vm.onResize, {
        passive: true
      });

      try {
        // server dvc và motcua độc lập
        vm.two_system = two_system_config;
      } catch (error) {}

      vm.dossierDetail = vm.detail;
      vm.getPaymentInfo();
      vm.loadDossierDetailMotcua();

      if (vm.dossierDetail.submissionNote) {
        var submissionNote = vm.dossierDetail.submissionNote ? JSON.parse(vm.dossierDetail.submissionNote) : '';
        var resultTemp = submissionNote ? submissionNote.data : '';

        if (resultTemp) {
          for (var i = 0; i < resultTemp.length; i++) {
            if (resultTemp[i].hasOwnProperty('actions') && resultTemp[i]['actions'] !== null && resultTemp[i]['actions'] !== undefined) {
              if (!Array.isArray(resultTemp[i]['actions'])) {
                var arrActionsTemp = [];
                arrActionsTemp.push(resultTemp[i]['actions']);
                resultTemp[i]['actions'] = arrActionsTemp;
              }
            }
          }

          vm.dossierActions = resultTemp;
        }
      }
    });
  },
  watch: {},
  methods: {
    loadDossierActions: function loadDossierActions() {
      var vm = this;
      var dataParams = {
        dossierId: vm.dossierDetail.dossierId
      };

      if (vm.dossierDetail.originality === 0 || vm.dossierDetail.originality === '0') {} else {
        vm.$store.dispatch('loadDossierActions', dataParams).then(function (resultActions) {
          if (resultActions.data) {
            var resultTemp = resultActions.data;

            for (var i = 0; i < resultTemp.length; i++) {
              if (resultTemp[i].hasOwnProperty('actions') && resultTemp[i]['actions'] !== null && resultTemp[i]['actions'] !== undefined) {
                if (!Array.isArray(resultTemp[i]['actions'])) {
                  var arrActionsTemp = [];
                  arrActionsTemp.push(resultTemp[i]['actions']);
                  resultTemp[i]['actions'] = arrActionsTemp;
                }
              }
            }

            vm.dossierActions = resultTemp;
          }
        });
      }
    },
    loadLogs: function loadLogs() {
      var vm = this;
      var filter = {
        dossierId: vm.dossierDetail.dossierId
      };
      vm.$store.dispatch('getListHistoryProcessingItems', filter).then(function (result) {
        vm.listHistoryProcessing = [];
        vm.listHistoryProcessing = result;
      });
    },
    loadDossierDetailMotcua: function loadDossierDetailMotcua() {
      var vm = this;

      if (vm.two_system) {
        vm.$store.dispatch('loadDetailDossierMC', vm.dossierDetail).then(function (result) {
          console.log('loadDetailDossierMC', result);
          vm.dossierDetailMotcua = result[0];
        }).catch(function (reject) {});
      }
    },
    loadVoting: function loadVoting() {
      var vm = this;
      console.log('dossierDetailMotcua', vm.dossierDetailMotcua);
      var filter = {
        className: 'dossier',
        classPK: vm.two_system ? vm.dossierDetailMotcua.dossierId : vm.dossierDetail.dossierId,
        serverCode: vm.dossierDetailMotcua['govAgencyCode']
      };

      if (vm.two_system) {
        vm.$store.dispatch('loadVotingMC', filter).then(function (result) {
          vm.votingItems = result;
          console.log('votingItems', vm.votingItems);
        }).catch(function (reject) {});
      } else {
        vm.$store.dispatch('loadVoting', filter).then(function (result) {
          vm.votingItems = result;
          console.log('votingItems', vm.votingItems);
        }).catch(function (reject) {});
      }
    },
    submitVoting: function submitVoting() {
      var vm = this;
      var arrAction = [];

      if (vm.votingItems.length > 0) {
        vm.loadingVoting = true;

        for (var index in vm.votingItems) {
          vm.votingItems[index]['className'] = 'dossier';
          vm.votingItems[index]['classPk'] = vm.two_system ? vm.dossierDetailMotcua.dossierId : vm.dossierDetail.dossierId;
          vm.votingItems[index]['serverCode'] = 'SERVER_' + vm.dossierDetail['govAgencyCode'];

          if (!vm.two_system) {
            arrAction.push(vm.$store.dispatch('submitVoting', vm.votingItems[index]));
          } else {
            arrAction.push(vm.$store.dispatch('submitVotingMC', vm.votingItems[index]));
          }
        }

        Promise.all(arrAction).then(function (results) {
          vm.loadingVoting = false;
          toastr.success('Gửi đánh giá thành công');
          vm.loadVoting();
        }).catch(function (xhr) {
          vm.loadingVoting = false;
        });
      }
    },
    toKeyPay: function toKeyPay(item) {
      var vm = this;
      window.open(item, '_self');
    },
    getPaymentInfo: function getPaymentInfo() {
      var vm = this;
      var filter = {
        referenceUid: vm.dossierDetail.referenceUid,
        secretCode: vm.secretCode
      };
      vm.$store.dispatch('loadDossierPayments', filter).then(function (result) {
        console.log(result);

        if (result && result.paymentStatus && String(result.paymentStatus) !== '3' && String(result.paymentStatus) !== '5') {
          vm.paymentInfo = result;
        }
      });
    },
    currency: function currency(value) {
      if (value) {
        return value.toString().replace(/\B(?=(\d{3})+(?!\d))/g, '.') ? value.toString().replace(/\B(?=(\d{3})+(?!\d))/g, '.') : 0;
      }

      return 0;
    },
    getEPaymentProfile: function getEPaymentProfile(paymentProfile) {
      if (paymentProfile) {
        try {
          JSON.parse(paymentProfile);
          return JSON.parse(paymentProfile);
        } catch (e) {
          return '';
        }
      } else {
        return '';
      }
    },
    goBack: function goBack() {
      window.history.back();
    },
    onResize: function onResize() {
      var vm = this;
      vm.isMobile = window.innerWidth < 1024;
    }
  },
  filters: {
    dateTimeView: function dateTimeView(arg) {
      if (arg) {
        var value = new Date(Number(arg));
        return "".concat(value.getDate().toString().padStart(2, '0'), "/").concat((value.getMonth() + 1).toString().padStart(2, '0'), "/").concat(value.getFullYear(), " ").concat(value.getHours().toString().padStart(2, '0'), ":").concat(value.getMinutes().toString().padStart(2, '0'));
      } else {
        return '';
      }
    }
  }
};